.bootstrap-tagsinput {
  height: 100%;

  input {
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: none;
    background-color: transparent;
    color: #343a40;
    padding: 0 6px;
    margin: 0;
    border-radius: 3px;
    font-family: initial;
    font-size: initial;
    line-height: initial;
    width: 100%;
    word-wrap: break-word;
    white-space: break-spaces;
  }

  .badge {
    max-width: 100%;
    word-wrap: break-word;
    white-space: break-spaces;
    margin-bottom: 2px;
    border: 1px solid $border;

    [data-role="remove"] {
      margin-left: 8px;
      cursor: pointer;

      &::after {
        content: "×";
        background-color: rgba(0, 0, 0, 0.3);
        font-size: 12px;
        padding: 0px 4px;
        border-radius: 50%;
      }
    }
  }
}
.sticky-pin {
  box-shadow: 2px 3px 4.7px 0.3px rgba(0, 0, 0, 0.1);
}
.item-all-cat .row .item-all-card {
  box-shadow: none;
}
.footer.footer-style .social ul li {
  float: none;
}
.pull-end {
  float: right;
}
.pull-start {
  float: left;
}
#example,
#example2 {
  border-top: 0 !important;
}
.dataTables_wrapper {
  .select2-container {
    width: 60px !important;
  }
}
/********************Start rtl*******************/
.rtl {
  .nav-link.user-img {
    padding-right: 0.9rem !important;
  }
  .nav-search {
    .form-control.header-search {
      margin-right: auto;
      margin-left: -6px;
      border-right: inherit;
      border-left: 0;
    }
  }

  .header-navsearch {
    .nav-search {
      padding-left: 15px !important;
    }
  }

  .form-inline {
    .form-control {
      padding-left: 0;
      padding-right: 20px;
      margin-right: auto;
      margin-left: -6px;
      border-bottom-right-radius: 5px;
      border-top-right-radius: 5px;
      border-right: inherit;
      border-left: 0;
    }

    .btn {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
      border-bottom-left-radius: 3px;
      border-top-left-radius: 3px;
    }
  }

  .navbar-nav .input-icon-addon {
    right: auto;
    left: 8px;
  }

  .navbar .form-inline .btn {
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 3px;
    border-top-left-radius: 3px;
  }

  .nav-unread.badge {
    right: auto;
    left: 7px;
  }

  .user-tabs ul li {
    border-left: 0;
    border-right: 1px solid rgba(167, 180, 201, 0.3);

    &:first-child {
      border-left: inherit;
      border-right: 0;
    }
  }

  .absolute-header-button {
    right: auto;
    left: 10px;
  }

  @media (max-width: 767px) {
    #count-down-1 {
      .clock-presenter {
        float: right;
      }

      .hours_dash {
        border-right: inherit;
        border-left: none;
      }
    }
  }

  @media (max-width: 992px) {
    .form-inline .nav-search {
      left: 24px;
      right: 0;

      .btn {
        border-radius: 3px 0 0 3px;
      }

      .form-control {
        float: right;
      }

      .btn {
        border-radius: 3px 0 0 3px;
      }
    }
  }

  @media (max-width: 495px) {
    .nested-media .media-body {
      margin-left: auto;
      margin-right: 0px;
    }
  }

  @media (max-width: 767px) {
    .header-right {
      .responsive-navbar .navbar-collapse .dropdown-menu.dropdown-menu-arrow.dropdown-menu-end {
        right: 0 !important;
        left: auto !important;
      }

      .navresponsive-toggler {
        margin-right: auto;
        margin-left: 0.5rem;
      }
    }

    .app-header1 .header-brand {
      text-align: right;
    }
  }

  @media (max-width: 767px) {
    .header-right-icons .responsive-navbar .navbar-collapse {
      left: auto;
      right: 0;
    }
  }
  #navbarSupportedContent-4 {
    .dropdown {
      .dropdown-menu-end {
        a.dropdown-item {
          strong {
            float: right;
            margin-left: 5px;
          }
        }
      }
    }
  }
  .item3-medias {
    .meida-md {
      .media-body {
        .item3-lists {
          li {
            a {
              .icon {
                float: right;
                margin-top: 5px;
              }
            }
          }
        }
      }
    }
  }
  .mail-option {
    .inbox-pagination {
      li {
        .np-btn {
          i {
            transform: rotate(180deg);
          }
        }
      }
    }
  }
  .bootstrap-tagsinput .badge [data-role="remove"] {
    margin-left: auto;
    margin-right: 8px;
  }
  .list-unstyled.leading-loose {
    li {
      i {
        display: inline-block;
      }
    }
  }
  .btn-list {
    .dropdown {
      .dropdown-toggle {
        i {
          float: right;
          margin-top: 5px;
        }
      }
    }
    &.btn-icon-list {
      button {
        i {
          float: right;
          margin-top: 5px;
        }
      }
    }
  }
  .carousel-control-prev {
    transform: rotate(0deg);
  }
  .carousel-control-next {
    transform: rotate(0deg);
  }
  .chart-line,
  .Crypto,
  .table-1,
  .table-3 {
    .table th:last-child,
    .table td:last-child {
      border-left-width: 0;
    }
  }
  .tab_wrapper > ul li {
    float: left;
  }
  .tab_wrapper.right_side > ul {
    float: right;
  }
  .tab_wrapper.right_side > ul li {
    border-right: 1px solid #eff4fc;
    border-left: 0;
    text-align: right;
  }
}

body.rtl {
  @media (max-width: 991px) {
    &.search-show .form-inline .absolute-header-button {
      right: auto;
      left: auto;
      border-radius: 5px 0 0 5px;
    }
  }
  @media (max-width: 1024px) {
    &.search-show .app-header1 {
      padding-left: inherit;
      padding-right: 25px;
    }
  }
}
/********************End rtl*******************/
